/************************************************

Stylesheet: Base Tegile Forms Stylesheet

*************************************************/


.form-container {

    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    border-radius: 4px;
    padding: 3rem;
    text-align: center;
    max-width: 480px;
    margin: 0 auto;
    position: relative;
    color: $graphite;

    & * {

        color: $graphite;
    }

    .form-not-you {

        margin-top: -1rem !important;
        display: block;
        margin-bottom: 1rem;
        font-size: .85rem;
        opacity: .5;

        &:hover {

            opacity: 1;
        }
    }

    .form-background {

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: url('data:image/png;base64,') #ededed;
        border-radius: 3px;
        z-index: -100;
        transform: translate3d(0, 0, 0);
    }

    .form-extras {

        &:before, &:after {

            content: "";
            display: block;
            position: absolute;
            background-repeat: no-repeat;
            background-position: bottom center;
            background-size: contain;
            z-index: -1;
            width: 100%;
            height: 100%;
        }
    }

    @media #{$small-screen} {

        padding: 2rem;
    }

    &.loading {

        opacity: 0;
    }

    h3 {

        font-size: $h2-font-size;
        font-weight: 900;

        @media #{$small-screen} {
            font-size: 2rem;
        }
    }

    .form-sub-text {

        font-size: $small-font-size;
        display: block;
        width: 80%;
        margin: 0 auto;
        margin-bottom: 2rem;

        a {

            text-decoration: underline;
        }
    }

    .form-fields-required {

        display: block;
        font-size: .75rem;
        opacity: .5;
    }

    form.tegile-form {
        
        color: $graphite !important;
        font-family: $input-font-family !important;
        width: auto !important;
        padding: 0 !important;

        & label {

            display: none !important;
        }

        & * {
            
            font-family: $input-font-family;
        }

        .form-field {

            margin-bottom: .5rem !important;

            .form-label {

                display: block;
                margin-top: 1.25rem;
                padding-bottom: .5rem;
                font-size: $h5-font-size;
                font-weight: $font-weight-semibold;
            }

            &.checkbox-list {

                margin-top: 2rem;

                .form-checkboxes {

                    margin-bottom: 0.75rem;

                    input {

                        width: auto !important;
                        display: inline;
                        line-height: normal;
                        min-height: auto;

                        &:focus {

                            transform: none !important;
                        }
                    }

                    .checkbox-label {

                        margin-left: 1.75em;
                        line-height: normal;
                        display: inline;
                    }
                }
            }

            input, select, textarea {
                
                color: #444 !important;
                font-size: $normal-font-size;
                padding: .5rem;
                border-radius: 3px;
                box-shadow: none;
                border: none;
                background-color: white;
                min-height: 3rem;
                opacity: .75;
                -webkit-transition: all 0.25s ease;
                transition: all 0.25s ease;

                &:focus {

                    box-shadow: 0 0 30px 4px rgba(255,255,255,.75) !important;
                    opacity: 1;
                    transform: scaleY(1.1) scaleX(1.05);
                }
            }

            textarea {

                margin-bottom: 1rem;
            }

            & .required-field {
                
                position: relative;

                &.warning {

                    input, select, textarea {

                        opacity: 1;
                        border: 1px solid rgba(255, 0, 0, 0.65);
                        box-shadow: -10px 0 25px -8px rgba(255,0,0,0.5);
                        font-weight: bold;
                        transform: translateX(.5rem);
                    }
                }
            }
        }

        input, div, select, textarea {

            width: 100% !important;
        }

        .submit-field {

            text-align: center;
            margin-top: 2rem;

            .submit-condition {

                display: block;
                font-size: $smallest-font-size;
                opacity: .75;
                margin: .5rem 0;
                margin-bottom: 1rem;
                text-align: center;

                a {

                    text-decoration: underline;
                }
            }

            button.form-submit {

                background-color: #0681ff;
                color: white;
                border-radius: 2px;

                &:hover {

                    background-color: darken($tegile-blue, 3%);
                }

                // &.disabled {

                //     opacity: .65;
                //     cursor: not-allowed;
                //     background-color: $graphite;
                //     color: $white;
                // }
            }
        }

        .mktoGutter, .mktoOffset {

            display: none !important;
        }

        .mktoError {

            display: none !important;
        }
    }
}

.formClear {

    clear: both;
    height: 4rem;
}