/************************************************

Stylesheet: Special Forms Stylesheet

*************************************************/

.form-container {

    /**********************
     *  Amazon Echo Form  *
    ***********************/

    &.echo-form {

        @media #{$medium-screen} {
            margin-bottom: 23rem;
        }

        @media #{$small-screen} {
            margin-bottom: 15rem;
        }

        + .formClear {
            height: 4rem !important;
        }

        color: $off-white;

        a, span, h1, h2, h3, button, i.fa {

            color: $off-white;
        }

        .form-background {

            background: linear-gradient(135deg, #00E598 0%, #1C83EA 100%);
        }

        .form-extras {
            
            &:before {

                background-image: url(/wp-content/uploads/2017/04/amazon-echo-dot-shadow.png);
                top: 13rem;
                left: -18rem;
                width: 100%;

                @media #{$medium-down-screen} {
                    z-index: 1;
                }

                @media #{$small-screen} {
                    left: -13rem;
                    top: 22rem;
                    width: 35rem;
                }
            }
        }

        .form-nav {

            .form-nav-button {

                color: $off-white;
                display: inline-block;
                padding: 1rem 2rem;
                border-radius: 2px;
                background-color: $coal;
                text-shadow: none;
                margin: .5rem;

                &:hover {

                    background-color: $charcoal;
                }
            }
        }
    }

    /**********************
     *  Rock Kit LP Form  *
    ***********************/

    &.rock-kit-form {

        + .formClear {
            height: 8rem !important;
        }

        color: $white;

        @media #{$medium-screen} {
            margin-bottom: 23rem;
        }

        @media #{$small-screen} {
            margin-bottom: 15rem;
            padding: 1rem;
        }

        a, span, h1, h2, h3, button, i.fa {

            color: $white;
        }
        
        .form-background {

            background: rgb(73,155,234);
            background: url('data:image/png;base64,'), -moz-radial-gradient(center, ellipse cover,  rgba(73,155,234,1) 0%, rgba(32,124,229,1) 100%);
            background: url('data:image/png;base64,'), -webkit-radial-gradient(center, ellipse cover,  rgba(73,155,234,1) 0%,rgba(32,124,229,1) 100%);
            background: url('data:image/png;base64,'), radial-gradient(ellipse at center,  rgba(73,155,234,1) 0%,rgba(32,124,229,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#499bea', endColorstr='#207ce5',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

        }
        
        .form-extras {
            
            &:before {

                background-image: url(/wp-content/uploads/2016/11/headphones-final.png);
                bottom: -5rem;
                left: -7rem;
                width: 75%;

                @media #{$medium-screen} {
                    bottom: -29rem;
                    left: 0;
                    z-index: 1;
                    height: 50%;
                }

                @media #{$small-screen} {
                    bottom: -25rem;
                    left: 0;
                    z-index: 1;
                    height: 23%;
                }
            }

            &:after {

                background-image: url(/wp-content/uploads/2016/11/stairway-shirt-final.png);
                bottom: -4rem;
                right: -15rem;
                width: 80%;
                transform: perspective(1400px) rotateX(5deg) rotateY(-15deg);

                @media #{$medium-screen} {
                    bottom: -25rem;
                    right: 0;
                    width: 100%;
                    height: 50%;
                }

                @media #{$small-screen} {
                    bottom: -23rem;
                    right: 0;
                    width: 100%;
                    height: 50%;
                }
            }
        }

        .form-nav {

            .form-nav-button {

                color: white;
                display: inline-block;
                padding: 1rem 2rem;
                border-radius: 2px;
                background-color: $turquoise;
                text-shadow: 1px 1px 4px rgba(0,0,0,0.35);
                margin: .5rem;

                &:hover {

                    background-color: darken($turquoise, 3%);
                }
            }
        }

        form.tegile-form {

            .checkbox-list {

                input {

                    &:checked {

                        box-shadow: 0 0 8px 2px rgb(255, 255, 255);
                    }
                }
            }

            .submit-field {

                text-align: center;

                .submit-condition {

                    text-align: center;
                }

                button.form-submit {

                    background-color: $turquoise;

                    &:hover {

                        background-color: darken($turquoise, 3%);
                    }
                }
            }
        }
    }
}